@media (min-width: 1600px) {
  .collapse__wrapper > * {
    font-size: 1.125rem;
  }
}
.collapse__wrapper[open] .collapse__summary {
  margin-bottom: 1rem;
}
.collapse__wrapper[open] .collapse__summary-text {
  font-family: "Helvetica-Neue-Bold", "Helvetica", "Arial", sans-serif;
  font-weight: 400;
  text-decoration: none;
}
.collapse__wrapper[open] .collapse__summary ~ * {
  animation: fade-in-top 0.25s cubic-bezier(0.39, 0.575, 0.565, 1) both;
}
@media screen and (prefers-reduced-motion: reduce) {
  .collapse__wrapper[open] .collapse__summary ~ * {
    transition: none;
  }
}
.collapse__summary {
  color: #00779d;
  font-size: 1.125rem;
  transition: letter-spacing 0.25s cubic-bezier(0.39, 0.575, 0.565, 1);
}
@media screen and (prefers-reduced-motion: reduce) {
  .collapse__summary {
    transition: none;
  }
}
.collapse__summary:hover {
  color: black;
  cursor: pointer;
}
.collapse__summary:focus {
  outline: none;
}
.collapse__summary:focus-visible {
  outline: 1px solid #00a0d2;
  outline-offset: 5px;
}
.collapse__summary-icon[class*=" icon-"]::before {
  font-size: 1.9rem;
  text-decoration: none;
  vertical-align: middle;
}
.collapse__summary-text {
  text-decoration: underline;
}
.collapse--grey .collapse__summary:not(:hover), .collapse--grey > * {
  color: #5c5c5c;
}
.collapse--narrow > * {
  margin-left: auto;
  margin-right: auto;
}
@media (min-width: 576px) {
  .collapse--narrow > * {
    width: 420px;
  }
}
@media (min-width: 768px) {
  .collapse--narrow > * {
    width: 570px;
  }
}
@media (min-width: 992px) {
  .collapse--narrow > * {
    width: 610px;
  }
}
@media (min-width: 1200px) {
  .collapse--narrow > * {
    width: 730px;
  }
}
@media (min-width: 1600px) {
  .collapse--narrow > * {
    width: 948px;
  }
}

@keyframes fade-in-top {
  0% {
    opacity: 0;
    transform: translateY(-20px);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}